<script lang="ts">
  import { createEventDispatcher } from 'svelte'
  import { Modal, Form, Slider, Button } from 'spaper'
  import { handleTxWithUx } from '../../helpers/tx-helper'
  import { withdraw, stake } from '../../helpers/eth-helper'

  export let active: boolean = false
  export let pid: number
  export let balance: number
  export let action: 'Withdraw' | 'Stake'

  const dispatch = createEventDispatcher()

  let value = 0

  async function confirmHandler() {
    await handleTxWithUx(
      action,
      action === 'Withdraw' ? withdraw : stake,
      [pid, value]
    )
  }

  function onclose() {
    value = 0
    dispatch('close')
  }
</script>

<Modal {active} title={action}
       on:close={onclose}>
  <Form>
    <p>Balance: {balance.toFixed(4)}</p>
    <Slider bind:value max={balance} step={1/1e18} block />
  </Form>
  <svelte:fragment slot="footer">
    <Button type="warning"
            on:click={() => value = balance}>
      Max
    </Button>
    <Button on:click={confirmHandler}>
      {action}
    </Button>
  </svelte:fragment>
</Modal>
