<script lang="ts">
  import type { Pool } from '../types'
  import { onMount } from 'svelte'
  import { Badge, Button, Card, Input, Form } from 'spaper'
  import { selectedAccount, userReady } from '../store'
  import SliderModal from '../components/SliderModal/index'
  import Loader from '../components/Loader.svelte'
  import {
    getPoolCount,
    getUserLPBalance,
    getPendingRewards,
    harvest,
    Address,
    withdrawAll,
    getStakableBalance,
  } from '../helpers/eth-helper'
  import { handleTxWithUx } from '../helpers/tx-helper'
  import { getPantherPrice } from '../helpers/price'
  import farms from '../farms'

  let pools: Pool[] = []
  let pantherPrice: number
  let searchText: string = ''
  let loading = true

  onMount(async () => {
    pantherPrice = await getPantherPrice()
    const poolsCount = await getPoolCount()
    selectedAccount.subscribe(async (account) => {
      await getUserPoolInfo(account, poolsCount)
      loading = false
    })
  })

  async function getUserPoolInfo(user: Address, poolsCount: number) {
    const rewardsTasks = []
    const stakedTasks = []
    const stakable = []

    for (let i=0; i<poolsCount; i++) {
      const tokens = farms[i]
      rewardsTasks[i] = getPendingRewards(i, user)
      stakedTasks[i] = getUserLPBalance(i, user)
      stakable[i] = getStakableBalance(i, user)
      pools[i] = {
        pid: i,
        rewards: 0,
        staked: 0,
        label: tokens.join(' - '),
        LP: tokens.length === 2,
        tokens,
        stakable: 0,
      }
    }
    for (let i=0; i<poolsCount; i++) {
      pools[i] = {
        ...pools[i],
        rewards: await rewardsTasks[i],
        staked: await stakedTasks[i],
        stakable: await stakable[i],
      }
    }
  }

  async function harvestHandler(pid: number) {
    await handleTxWithUx(
      `Harvesting ${pools[pid].label}`,
      harvest, [pid],
    )
  }

  async function withdrawAllHandler(pid: number) {
    await handleTxWithUx(
      `Withdrawing All from ${pools[pid].label}`,
      withdrawAll, [pid, $selectedAccount]
    )
  }

  $: displayedPools = (
    pools.filter(pool => (
      pool.tokens.some(token => (
        token.toLowerCase().includes(searchText.toLowerCase())
      ))
    ))
  )
  $: toHarvest = pools.reduce((acc, { rewards }) => (
    acc + (rewards * pantherPrice)
  ), 0)
</script>

<h1>farms & pools</h1>
{#if $userReady}
  {#if loading}
    <Loader />
  {:else}
    <Form>
      <Input placeholder="Searck token..."
             bind:value={searchText}
             block />
    </Form>
  {/if}
  {#if toHarvest}
    <Badge type="success">
      To Harvest: {toHarvest.toFixed(2)}$
    </Badge>
  {/if}

  <div class="pool-list">
    {#each displayedPools as { pid, rewards, staked, label, LP, tokens, stakable }}
      <Card class="border border-5" width="480px">
        <svelte:fragment slot="header">
          {#each tokens as token, i}
            <img src="img/{token.toLowerCase()}.png"
                 class="coin-logo margin-top"
                 alt={token}
                 style:z-index={tokens.length-i}
                 style:margin-left={i === 1 ? '-8px' : '0'} />
          {/each}
          <h4 class="margin-small">{label}</h4>
        </svelte:fragment>
        <p class="text-secondary">
          Staked: {staked.toFixed(3)} {LP ? 'LP Token': label}
        </p>
        <p>Pending Rewards: {Math.floor(rewards)} PANTHER</p>
        <p class="text-success">{(rewards * pantherPrice).toFixed(2)}$</p>

        <svelte:fragment slot="footer">
          <Button type="success" on:click={harvestHandler.bind(null, pid)}
                  disabled={!rewards}>
            Harvest
          </Button>
          <Button type="secondary" on:click={SliderModal.open.bind(null, {
            pid,
            balance: stakable,
            action: 'Deposit',
          })} disabled={!stakable}>
            Stake
          </Button>
          <Button type="secondary" on:click={SliderModal.open.bind(null, {
            pid,
            balance: staked,
            action: 'Withdraw',
          })} disabled={!staked}>
            Withdraw
          </Button>
          <Button type="warning" on:click={withdrawAllHandler.bind(null, pid)}
                  disabled={!staked}>
            Withdraw All
          </Button>
        </svelte:fragment>
      </Card>
    {/each}
  </div>
{/if}


<style lang="scss">
.pool-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  :global(& > .card) {
    margin: 1rem;
  }
  :global(.card-header) {
    text-align: center;
  }
}

.coin-logo {
  position: relative;
  display: inline-block;
  border-radius: 50%;
  width: 40px;
}
</style>